<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <!--  -->
      <el-form :model="searchForm" ref="addFormRef" label-width="60px">
        <el-row>
          <el-col :span="6">
            <el-form-item label="" prop="lyNumber">
              <el-select v-model="searchForm.lyNumber" placeholder="" clearable>
                <el-option
                    v-for="item in lyNamelist"
                    :key="item.id"
                    :label="item.lynumber"
                    :value="item.lynumber"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="3">
            <el-button type="primary" icon="el-icon-search" @click="search"></el-button>
          </el-col>
          <el-col :span="3">
            <el-button type="primary" icon="el-icon-plus" @click="addDialogVisible=true"></el-button>
          </el-col>
        </el-row>
      </el-form>
      <!--  -->
      <el-tabs type="border-card">
        <el-tab-pane label="">
          <!--  -->
          <el-table
            :data="datalist"
            stripe
            border
            maxheight="350px"
            :default-sort="{prop: 'time',prop: 'health',prop: 'discipline',prop: 'dormitoryculture',prop: 'total', order: 'descending'}"
          >
            <el-table-column label="" prop="lynumber" width="90px"></el-table-column>
            <el-table-column label="" prop="name" width="90px"></el-table-column>
            <el-table-column label="" prop="tel"></el-table-column>
            <el-table-column label="" prop="bxsh" sortable></el-table-column>
            <el-table-column label="" prop="clgm" sortable></el-table-column>
            <el-table-column label="" prop="lywh" sortable></el-table-column>
            <el-table-column label="" prop="total" sortable></el-table-column>
          </el-table>
          <!--  -->
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="queryInfo.pagenum"
            :page-sizes="[1, 2, 5, 10]"
            :page-size="queryInfo.pagesize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="total"
          ></el-pagination>
        </el-tab-pane>
        <el-tab-pane label="" name="second">
          <!--  -->
          <div id="myChart"></div>
        </el-tab-pane>
      </el-tabs>
      <!--  -->
      <el-dialog title="" :visible.sync="addDialogVisible" width="50%" @close="addDialogClosed">
        <!--  -->
        <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="80px">
          <el-form-item label="" prop="lyNumber">
            <el-select v-model="addForm.lyNumber" placeholder="" clearable>
              <el-option
                  v-for="item in lyNamelist"
                  :key="item.id"
                  :label="item.lynumber"
                  :value="item.lynumber"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="name">
            <el-input v-model="addForm.name"></el-input>
          </el-form-item>
          <el-form-item label="" prop="tel">
            <el-input v-model="addForm.tel"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="addForm.bxsh"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="addForm.clgm"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="addForm.lywh"></el-input>
          </el-form-item>
        </el-form>
        <!--  -->
        <span slot="footer" class="dialog-footer">
          <el-button @click="addDialogVisible = false"> </el-button>
          <el-button type="primary" @click="addUser"> </el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    // 
    var checkPhone = (rule, value, cb) => {
      // 
      // const regPhone = /^(0|86|17951)?(13[0-9]|15[0123456789]|17[678]|18[0-9]|14[57])[0-9]{8}$/
      const regPhone = /^[1][3,4,5,7,8][0-9]{9}$/;
      if (regPhone.test(value)) {
        return cb();
      }
      cb(new Error(""));
    };
    return {
      // 
      lyNamelist:[],
      // 
      searchForm: {
        lyNumber: ""
      },
      // 
      addForm: {
        lyNumber: "",
        name:'',
        tel:'',
        bxsh:'',
        clgm:'',
        lywh:''
      },
      // 
      addFormRules: {
        name: [
          { required: true, message: "", trigger: "blur" },
          { min: 2, max: 10, message: " 2  10 ", trigger: "blur" }
        ],
        tel: [
          { required: true, message: "", trigger: "blur" },
          { validator: checkPhone, trigger: "blur" }
        ],
        fwf: [
          { required: true, message: "", trigger: "blur" }
        ],
        zrx: [
          { required: true, message: "", trigger: "blur" }
        ],
        pgf: [
          { required: true, message: "", trigger: "blur" }
        ],
        lyNumber: [
          {
            required: true,
            message: ",*",
            trigger: "blur"
          }
        ]
      },
      // 
      datalist: [],
      queryInfo: {
        // 
        pagenum: 1,
        // 
        pagesize: 5
      },
      // 
      lyh: [],
      // 
      bxsh: [],
      // 
      clgm: [],
      // 
      lywh: [],
      // 
      total: 0,
      // 
      activeName: "second",
      // 
      addDialogVisible: false,
    };
  },
  created() {
    this.oneQuest();
    var _this=this;
    this.$http
        .post("http://localhost/buildings/lyname")
        .then(function(response) {
          console.log(response.data);
          if (response.data.code == 200) {
            _this.lyNamelist = response.data.data;
          }
        });
  },
  methods: {
    // 
    oneQuest() {
      var _this = this;
      this.$http
        .post("http://localhost/bxtj/search", {
          lynumber: _this.searchForm.lyNumber,
          pagenum: _this.queryInfo.pagenum,
          pagesize: _this.queryInfo.pagesize
        })
        .then(function(response) {
          console.log(response.data);
          if (response.data.code == 200) {
            _this.datalist = response.data.data;
            _this.total = response.data.total;
            _this.lyh = response.data.lyh;
            _this.bxsh = response.data.bxsh;
            _this.clgm = response.data.clgm;
            _this.lywh = response.data.lywh;
            //  echarts 
            _this.drawColumn();
          }
        });
    },
    // 
    search(){
      this.oneQuest();
    },
    // pageSize
    handleSizeChange(newSize) {
      console.log(newSize);
      this.queryInfo.pagesize = newSize;
      this.oneQuest();
    },
    // 
    handleCurrentChange(newPage) {
      console.log(newPage);
      this.queryInfo.pagenum = newPage;
      this.oneQuest();
    },
    // 
    addDialogClosed() {
      // this.$refs.addFormRef.resetFields();
    },
    //
    addUser() {
      var _this = this;
      this.$refs.addFormRef.validate(valid => {
        console.log(valid);
        if (!valid) return;
        // 
        // this.studentlist.unshift(this.addForm);
        this.$http
          .post("http://localhost/bxtj/add", this.addForm)
          .then(function(response) {
            console.log(response.data);
            if (response.data.code == 200) {
              _this.oneQuest();
              _this.$message({
                showClose: true,
                message: "",
                type: "success"
              });
            }else{
              _this.$message({
                showClose: true,
                message: "",
                type: "error"
              });
            }
          });
        this.addDialogVisible = false;
      });
    },

    // echarts
    drawColumn() {
      let myChart = this.$echarts.init(document.getElementById("myChart"));
      var option = {
        title: {
          text: "",
          left: "center",
          top: "auto"
        },
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "cross",
            crossStyle: {
              color: "#999"
            }
          }
        },
        toolbox: {
          feature: {
            dataView: { show: true, readOnly: false },
            magicType: { show: true, type: ["line", "bar"] },
            restore: { show: true },
            saveAsImage: { show: true }
          }
        },
        legend: {
          data: ["", "", ""],
          top: "30px"
        },
        xAxis: [
          {
            type: "category",
            name: "",
            data: this.lyh,
            axisPointer: {
              type: "shadow"
            }
          }
        ],
        yAxis: [
          {
            type: "value",
            name: "",
            min: 0,
            max: 10000,
            interval: 1000,
            axisLabel: {
              formatter: "{value} "
            }
          },
          {
            type: "value",
            name: "",
            min: 0,
            max: 10000,
            interval: 1000,
            axisLabel: {
              formatter: "{value} "
            }
          },
          {
            type: "value",
            name: "",
            min: 0,
            max: 10000,
            position: "right",
            offset: 50,
            axisLine: {
              lineStyle: {
                color: "red"
              }
            },
            axisLabel: {
              formatter: "{value} "
            }
          }
        ],
        series: [
          {
            name: "",
            type: "bar",
            data: this.bxsh
          },
          {
            name: "",
            type: "bar",
            data: this.clgm
          },
          {
            name: "",
            type: "line",
            yAxisIndex: 1,
            data: this.lywh
          }
        ]
      };
      myChart.setOption(option);
    }
  }
};
</script>

<style lang="css" scoped>
.el-button {
  margin-left: 20px;
}
#myChart {
  width: 950px;
  height: 450px;
}
</style>
