<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <el-form label-width="60px">
        <el-row>
          <!--  -->
          <el-col :span="8">
            <el-form-item label="">
              <el-select v-model="lynumber" placeholder="" clearable>
                <el-option
                  v-for="item in lyNamelist"
                  :key="item.id"
                  :label="item.lynumber"
                  :value="item.lynumber"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <!--  -->
          <!--  -->
          <el-col :span="6">
            <el-button type="primary" icon="el-icon-search" @click="search"></el-button>
          </el-col>
        </el-row>
      </el-form>
    </el-card>
    <!--  -->
    <el-card>
      <!--  -->
      <el-table :data="lylist" stripe border>
        <el-table-column type="index"></el-table-column>
        <el-table-column label="" prop="lynumber"></el-table-column>
        <el-table-column label="" prop="leader"></el-table-column>
        <el-table-column label="" prop="sex"></el-table-column>
        <el-table-column label="" prop="tel"></el-table-column>
        <el-table-column label="" width="200px">
          <template slot-scope="scope">
            <!--  -->
            <el-tooltip effect="dark" content="" placement="top" :enterable="false">
              <el-button
                type="warning"
                icon="el-icon-edit"
                size="mini"
                @click="showEditDialog(scope.row)"
              ></el-button>
            </el-tooltip>
            <!--  -->
            <el-tooltip effect="dark" content="" placement="top" :enterable="false">
              <el-button
                type="danger"
                icon="el-icon-delete"
                size="mini"
                @click="removelyInfoById(scope.row.id)"
              ></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 2, 5, 10]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
      <!--  -->
      <el-dialog title="" :visible.sync="addDialogVisible" width="50%" @close="addDialogClosed">
        <!--  -->
        <el-form :model="addForm" ref="addFormRef" :rules="addFormRules" label-width="80px">
          <el-form-item label="" prop="lynumber">
            <el-input v-model="addForm.lynumber"></el-input>
          </el-form-item>
          <el-form-item label="" prop="leader">
            <el-input v-model="addForm.leader"></el-input>
          </el-form-item>
          <el-form-item label="" prop="sex">
            <el-select v-model="addForm.sex" placeholder="">
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="tel">
            <el-input v-model="addForm.tel"></el-input>
          </el-form-item>
        </el-form>
        <!--  -->
        <span slot="footer" class="dialog-footer">
          <el-button @click="addDialogVisible = false"> </el-button>
          <el-button type="primary" @click="addUser"> </el-button>
        </span>
      </el-dialog>
      <!-- / -->
      <el-dialog
        title="/"
        :visible.sync="editdialogVisible"
        width="50%"
        @close="editDialogClosed"
      >
        <!--  -->
        <el-form :model="editForm" ref="editFormRef" :rules="editFormRules" label-width="120px">
          <el-form-item label="" prop="lynumber">
            <el-input v-model="editForm.lynumber" disabled></el-input>
          </el-form-item>
          <el-form-item label="" prop="leader">
            <el-input v-model="editForm.leader" clearable></el-input>
          </el-form-item>
          <el-form-item label="" prop="sex">
            <el-input v-model="editForm.sex" clearable></el-input>
          </el-form-item>
          <el-form-item label="" prop="tel">
            <el-input v-model="editForm.tel" clearable></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="editdialogVisible = false"> </el-button>
          <el-button type="primary" @click="editUserInfo"> </el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    // 
    var checkPhone = (rule, value, cb) => {
      // 
      // const regPhone = /^(0|86|17951)?(13[0-9]|15[0123456789]|17[678]|18[0-9]|14[57])[0-9]{8}$/
      const regPhone = /^[1][3,4,5,7,8][0-9]{9}$/;
      if (regPhone.test(value)) {
        return cb();
      }
      cb(new Error(""));
    };
    return {
      // 
      lylist: [],
      // 
      lyNamelist: [],
      // select 
      lynumber: "",
      // 
      userName: "",
      queryInfo: {
        // 
        query: "",
        // 
        pagenum: 1,
        // 
        pagesize: 5
      },
      // 
      total: 0,
      // 
      addDialogVisible: false,
      // 
      addForm: {
        lynumber: "",
        leader: "",
        sex: "",
        tel: ""
      },
      addexl:[],
      // 
      addFormRules: {
        lynumber: [
          { required: true, message: "", trigger: "blur" }
        ],
        leader: [
          { required: true, message: "", trigger: "blur" }
        ],
        sex: [{ required: true, message: "", trigger: "blur" }],
        tel: [
          { required: true, message: "", trigger: "blur" },
          { validator: checkPhone, trigger: "blur" }
        ]
      },
      // 
      editdialogVisible: false,
      // 
      editForm: {
        id: "",
        lynumber: "",
        leader: "",
        sex: "",
        tel: ""
      },
      // 
      editFormRules: {
        leader: [
          { required: true, message: "", trigger: "blur" }
        ],
        tel: [
          { required: true, message: "", trigger: "blur" },
          { validator: checkPhone, trigger: "blur" }
        ]
      }
    };
  },
  methods: {
    // pageSize
    handleSizeChange(newSize) {
      console.log(newSize);
      this.queryInfo.pagesize = newSize;
      var _this = this;
      this.$http
        .post("http://localhost/buildings", {
          pagenum: this.queryInfo.pagenum,
          pagesize: newSize
        })
        .then(function(response) {
          console.log(response.data);
          if (response.data.code == 200) {
            _this.lylist = response.data.data;
            _this.total = response.data.total;
          }
        });
    },
    // 
    handleCurrentChange(newPage) {
      // console.log(newPage);
      this.queryInfo.pagenum = newPage;
      var _this = this;
      this.$http
        .post("http://localhost/buildings", {
          pagenum: newPage,
          pagesize: this.queryInfo.pagesize
        })
        .then(function(response) {
          console.log(response.data);
          if (response.data.code == 200) {
            _this.lylist = response.data.data;
            _this.total = response.data.total;
          }
        });
    },
    // 
    addDialogClosed() {
      this.$refs.addFormRef.resetFields();
    },
    // 
    addUser() {
      var _this = this;
      this.$refs.addFormRef.validate(valid => {
        console.log(valid);
        if (!valid) return;
        // 
        this.$http
          .post("http://localhost/buildings/add", this.addForm)
          .then(function(response) {
            console.log(response.data);
            if (response.data.code == 200) {
              _this.$notify({
                title: "",
                message: response.data.msg,
                type: "success"
              });
              // 
              _this.$http
                .post("http://localhost/buildings", _this.queryInfo)
                .then(function(response) {
                  console.log(response.data);
                  if (response.data.code == 200) {
                    _this.lylist = response.data.data;
                    _this.total = response.data.total;
                  }
                });
              _this.addDialogVisible = false;
            } else {
              _this.$notify({
                title: "",
                message: response.data.msg,
                type: "error"
              });
              _this.addDialogVisible = false;
            }
          });
      });
    },
    // 
    showEditDialog(user) {
      var _this = this;
      this.editdialogVisible = true;
      console.log(user);
      // id
      this.$http
        .post("http://localhost/buildings/show", { id: user.id })
        .then(function(response) {
          console.log(response.data);
          _this.editForm = response.data.data[0];
        });
    },

    // 
    editUserInfo() {
      var _this = this;
      this.$refs.editFormRef.validate(valid => {
        console.log(valid);
        if (!valid) return;
        // 
        this.$http
          .post("http://localhost/buildings/edit", this.editForm)
          .then(function(response) {
            console.log(response.data);
            if (response.data.code == 200) {
              _this.$message({
                showClose: true,
                message: "",
                type: "success"
              });
            }
            if (response.data.code == 100) {
              _this.$message({
                showClose: true,
                message: "",
                type: "info"
              });
            }
            if (response.data.code == 0) {
              _this.$message({
                showClose: true,
                message: "",
                type: "error"
              });
            }
          });
        this.editdialogVisible = false;
      });
    },

    // 
    editDialogClosed() {
      // this.$refs.editFormRef.resetFields();
    },

    // id
    async removelyInfoById(id) {
      // console.log(id);
      var _this = this;
      var _thisPost = this.$http;
      // 
      const cofirmResult = await this.$confirm(
        ", ?",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        }
      ).catch(err => err);
      //  confirm
      //  cancel
      // console.log(cofirmResult)
      if (cofirmResult !== "confirm") {
        return this.$message({
          showClose: true,
          message: "",
          type: "info"
        });
      }
      // 
      this.$http
        .post("http://localhost/buildings/delete", {
          id: id,
          pagenum: this.queryInfo.pagenum,
          pagesize: this.queryInfo.pagesize
        })
        .then(function(response) {
          console.log(response.data);
          if (response.data.code == 200) {
            _this.lylist = response.data.newResult;
            _this.total = response.data.total;
            _this.$message({
              showClose: true,
              message: "",
              type: "error"
            });

            // 
            _thisPost
              .post("http://localhost/buildings/lyname")
              .then(function(response) {
                console.log(response.data);
                if (response.data.code == 200) {
                  _this.lyNamelist = response.data.data;
                }
              });
          }
        });
    },
    // 
    search() {
      var _this = this;
      // 
      this.$http
        .post("http://localhost/buildings/search", {
          lynumber: this.lynumber,
          userName: this.userName
        })
        .then(function(response) {
          console.log(response.data);
          if (response.data.code == 200) {
            _this.lylist = response.data.data;
            _this.total=1;
          } else {
            _this.$notify({
              title: "",
              message: "",
              type: "warning"
            });
          }
        });
    }
  },
  created() {
    var _this = this;
    // 
    this.$http
      .post("http://localhost/buildings", this.queryInfo)
      .then(function(response) {
        console.log(response.data);
        if (response.data.code == 200) {
          _this.lylist = response.data.data;
          _this.total = response.data.total;
        }
      });
    // 
    this.$http
      .post("http://localhost/buildings/lyname")
      .then(function(response) {
        console.log(response.data);
        if (response.data.code == 200) {
          _this.lyNamelist = response.data.data;
        }
      });
  }
};
</script>

<style lang="css" scoped>
.el-button {
  margin-left: 30px;
}
.cardBg {
  background: red;
}
</style>
