              formatter: "{value} "
            }
          },
          {
            type: "value",
            // name: "",
            min: 0,
            max: 3,
            interval: 1,
            axisLabel: {
              formatter: "{value} "
            }
          },
          {
            type: "value",
            // name: "",
            min: 0,
            max: 3,
            interval: 1,
            axisLabel: {
              formatter: "{value} "
            }
          },
          {
            type: "value",
            // name: "",
            min: 0,
