            interval: 1,
            axisLabel: {
              formatter: "{value} "
            }
          },
          {
            type: "value",
            // name: "",
            min: 0,
            max: 2,
            interval: 1,
            axisLabel: {
              formatter: "{value} "
            }
          },
          {
            type: "value",
            // name: "",
            min: 0,
            max: 2,
            interval: 1,
            axisLabel: {
              formatter: "{value} "
            }
          },
          {
            type: "value",
            // name: "",
            min: 0,
            max: 2,
            interval: 1,
            axisLabel: {
              formatter: "{value} "
            }
          },
          {
            type: "value",
            // name: "",
            min: 0,
            max: 2,
            interval: 1,
            axisLabel: {
              formatter: "{value} "
            }
          },
