            type: "bar",
            data: this.beizi
          },
          {
            name: "",
            type: "bar",
            data: this.zhuomian
          },
          {
            name: "",
            type: "bar",
            data: this.xiezi
          },
          {
            name: "",
            type: "bar",
            data: this.dimian
          },
          {
            name: "",
            type: "bar",
            data: this.yangtai
          },
          {
