<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <!--  -->
      <el-table :data="repairsList" stripe border max-height="500px">
        <el-table-column type="index" width="40px"></el-table-column>
        <el-table-column label="" prop="lynumber" width="90px"></el-table-column>
        <el-table-column label="" prop="sushe" width="60px"></el-table-column>
        <el-table-column label="" prop="uname" width="60px"></el-table-column>
        <el-table-column label="" prop="tel" width="100px"></el-table-column>
        <el-table-column label="" prop="bxdate" width="100px"></el-table-column>
        <el-table-column label="" prop="bxtime" width="100px"></el-table-column>
        <el-table-column label="" prop="bxdel"></el-table-column>
        <el-table-column label="" width="120px">
          <template slot-scope="scope">
            <!--  -->
            <el-tooltip
              effect="dark"
              :content="scope.row.switchon =='1'?'':''"
              placement="top"
              :enterable="false"
            >
              <el-switch
                v-model="scope.row.switchon"
                active-value="1"
                inactive-value="0"
                @change="userStateChanged(scope.row)"
              ></el-switch>
            </el-tooltip>
            <!--  -->
            <el-tooltip effect="dark" content="" placement="top" :enterable="false">
              <el-button
                type="danger"
                icon="el-icon-delete"
                size="mini"
                @click="removeUserById(scope.row.id)"
              ></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 2, 5, 10]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 
      repairsList: [],
      // 
      queryInfo: {
        // 
        query: "",
        // 
        pagenum: 1,
        // 
        pagesize: 5
      },
      // 
      total: 0,
      // 
      switchValue: true
    };
  },
  created() {
    this.oneQuest();
  },
  methods: {
    // 
    oneQuest() {
      var _this = this;
      this.$http
        .post("http://localhost/repairReport/search", this.queryInfo)
        .then(function(response) {
          console.log(response.data);
          if (response.data.code == 200) {
            _this.repairsList = response.data.data;
            _this.total = response.data.total;
          }
        });
    },
    // pageSize
    handleSizeChange(newSize) {
      console.log(newSize);
      this.queryInfo.pagesize = newSize;
      this.oneQuest();
    },
    // 
    handleCurrentChange(newPage) {
      console.log(newPage);
      this.queryInfo.pagenum = newPage;
      this.oneQuest();
    },
    //  switch
    async userStateChanged(userInfo) {
      var _this = this;
      console.log(userInfo);
      // id
      this.$http
        .post("http://localhost/repairReport/onoff", {
          id: userInfo.id,
          switchon: userInfo.switchon
        })
        .then(function(response) {
          console.log(response.data);
          if (response.data.code == 200) {
            _this.oneQuest();
          }
        });
    },
    // 
    async removeUserById(id) {
      var _this = this;
      // 
      const cofirmResult = await this.$confirm(
        ", ?",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        }
      ).catch(err => err);
      //  confirm
      //  cancel
      // console.log(cofirmResult)
      if (cofirmResult !== "confirm") {
        return this.$message({
          showClose: true,
          message: "",
          type: "info"
        });
      }
      // id
      this.$http
        .post("http://localhost/repairReport/del", {
          id: id
        })
        .then(function(response) {
          console.log(response.data);
          if (response.data.code == 200) {
            _this.oneQuest();
            return _this.$message({
              showClose: true,
              message: "",
              type: "success"
            });
          }
        });
    }
  }
};
</script>

<style lang="css" scoped>
.el-button {
  margin-left: 15px;
}
.el-table .success-row {
  background: #f0f9eb;
}
</style>
