<template>
  <div class="bg">
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <el-form :model="userForm" ref="userFormRef" label-width="80px">
        <el-row>
          <el-col :span="6">
            <el-form-item label="" prop="name">
              <el-input v-model="userForm.name" clearable disabled></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="" prop="sex">
              <el-input v-model="userForm.sex" clearable disabled></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="" prop="uname">
              <el-input v-model="userForm.uname" clearable disabled></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="" prop="xueyuan">
              <el-input v-model="userForm.xueyuan" clearable disabled></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="6">
            <el-form-item label="" prop="banji">
              <el-input v-model="userForm.banji" clearable disabled></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="" prop="tel">
              <el-input v-model="userForm.tel" clearable :disabled="editable"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="" prop="lynumber">
              <el-input v-model="userForm.lynumber" clearable disabled></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="" prop="sushe">
              <el-input v-model="userForm.sushe" clearable disabled></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="10">
            <el-form-item label="" prop="pass">
              <el-input
                v-model="userForm.pass"
                show-password
                clearable
                :disabled="editable"
                placeholder=""
              ></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <!--  -->
        <el-row type="flex" justify="end">
          <el-button type="warning" @click="editUserinfo"></el-button>
          <el-button type="success" :disabled="editable" @click="submit"></el-button>
          <el-button type="primary" :disabled="editable" @click="cancel"></el-button>
        </el-row>
      </el-form>
    </el-card>
    <el-card v-if="access">
      <el-row>
        <!--  -->
        <el-col :span="12">
          <div slot="header" class="clearfix">
            <span></span>
          </div>
          <div style="display: flex; align-items: center;">
            <!--  icon -->
            <div style="flex: 1;">
              <img src="../../assets/img/dianfei.png" alt="" style="width: 125px; height: 125px;">
            </div>
            <!--  -->
            <div style="flex: 3;font-size: 20px;font-weight: bold;line-height: 40px" >
              <!--  -->
              <p>{{ electricityInfo.dianfei }}</p>
              <p>{{ electricityInfo.date }}</p>
              <p>{{ electricityInfo.status }}</p>
            </div>
          </div>
        </el-col>
        <!--  -->
        <el-col :span="12">
          <div slot="header" class="clearfix">
            <span></span>
          </div>
          <div style="display: flex; align-items: center;">
            <!--  icon -->
            <div style="flex: 1;">
              <img src="../../assets/img/rate.png" alt="" style="width: 125px; height: 125px;">
            </div>
            <!--  -->
            <div style="flex: 3;font-size: 20px;font-weight: bold;line-height: 40px" >
              <!--  -->
              <p>{{ pinfen.rate }}</p>
              <p>{{ pinfen.date }}</p>
              <p style="color: red;font-size: 12px">-</p>
            </div>
          </div>
        </el-col>
      </el-row>
    </el-card>

  </div>
</template>

<script>
export default {
  data() {
    return {
      // 
      userForm: {},
      // 
      editable: true,
      // 
      access:1,
      // 
      electricityInfo: {
        dianfei: '',
        date: '',
        status: ''
      },
      pinfen:{
        rate:'',
        date:'',
      }
    };
  },
  methods: {
    // 
    editUserinfo() {
      this.editable = false;
    },
    // 
    submit() {
      var _this = this;
      // 
      this.$http
        .post("http://localhost/login/update", this.userForm)
        .then(function(response) {
          console.log(response.data);
          if (response.data.code == "200") {
            // ,
            _this.$message({
              showClose: true,
              message: "",
              type: "success"
            });
            var userData = JSON.stringify(response.data.newResult[0]);
            window.sessionStorage.setItem("userData", userData);
            _this.editable = true;
          }else{
            // ,
            _this.$message({
              showClose: true,
              message: "",
              type: "error"
            });
          }
        });
      // alert("");
    },
    // 
    cancel() {
      this.userForm = JSON.parse(window.sessionStorage.getItem("userData"));
      this.editable = true;
    }
  },
  created() {
    this.userForm = JSON.parse(window.sessionStorage.getItem("userData"));
    this.access = this.userForm.roler == 2 ? 1 : 0;
    var _this = this;
    // 
    if (_this.access){
    this.$http
        .post("http://localhost/personal/dianfei", this.userForm)
        .then(function (response) {
          console.log(response.data.data[0]);
          if (response.data.code == "200") {
            // ,
            var userData = response.data.data[0];
            userData.dianfei = userData.dianfei + "";
            if (userData.status == 0) {
              userData.status = "";
            } else {
              userData.status = "";
            }
            userData.date = userData.date.substring(0, 10);
            _this.electricityInfo = userData;
          } else {
            // ,
            _this.$message({
              showClose: true,
              message: "",
              type: "error"
            });
          }
        });
    this.$http
        .post("http://localhost/personal/pinfen", this.userForm)
        .then(function(response) {
          console.log(response.data.data[0]);
          if (response.data.code == "200") {
            // ,
            var userData = response.data.data[0];
            _this.pinfen={
              rate:userData.total,
              date:userData.time
            }
          }else{
            // ,
            _this.$message({
              showClose: true,
              message: "",
              type: "error"
            });
          }
        });
  }}
};
</script>

