<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <!--  -->
      <el-row :gutter="30">
        <el-col :span="8">
          <el-input placeholder="" v-model="queryInfo.query" clearable>
            <el-button slot="append" icon="el-icon-search" @click="search(queryInfo.query)"></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="addDialogVisible=true"></el-button>
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data="userlist" stripe border height="420">
        <el-table-column type="index"></el-table-column>
        <el-table-column label="" prop="uname"></el-table-column>
        <el-table-column label="" prop="name"></el-table-column>
        <el-table-column label="" prop="sex"></el-table-column>
        <el-table-column label="" prop="tel"></el-table-column>
        <el-table-column label="" prop='roler' :formatter="formatRole"></el-table-column>
        <el-table-column label="" id="cz">
          <template slot-scope="scope">
            <!--  -->
            <el-tooltip effect="dark" content="" placement="top" :enterable="false">
              <el-button
                type="primary"
                icon="el-icon-edit"
                size="mini"
                @click="showEditDialog(scope.row)"
              ></el-button>
            </el-tooltip>
            <!--  -->
            <el-tooltip effect="dark" content="" placement="top" :enterable="false">
              <el-button
                type="danger"
                icon="el-icon-delete"
                size="mini"
                @click="removeUserById(scope.row.uname)"
              ></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 2, 5, 7,10]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
      <!--  -->
      <el-dialog title="" :visible.sync="addDialogVisible" width="50%" @close="addDialogClosed">
        <!--  -->
        <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="80px">
          <el-form-item label="" prop="userId">
            <el-input v-model="addForm.userId"></el-input>
          </el-form-item>
          <el-form-item label="" prop="userName">
            <el-input v-model="addForm.userName"></el-input>
          </el-form-item>
          <el-form-item label="" prop="sex">
            <el-select v-model="addForm.sex" placeholder="">
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="telephone">
            <el-input v-model="addForm.telephone"></el-input>
          </el-form-item>
          <el-form-item label="" prop="roler">
            <el-select v-model="addForm.roler" placeholder="">
              <el-option label="" value="0"></el-option>
              <el-option label="" value="1"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <!--  -->
        <span slot="footer" class="dialog-footer">
          <el-button @click="addDialogVisible = false"> </el-button>
          <el-button type="primary" @click="addUser"> </el-button>
        </span>
      </el-dialog>
      <!--  -->
      <el-dialog
        title=""
        :visible.sync="editdialogVisible"
        width="50%"
        @close="editDialogClosed"
      >
        <!--  -->
        <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="80px">
          <el-form-item label="" prop="">
            <el-input v-model="editForm.uname" disabled></el-input>
          </el-form-item>
          <el-form-item label="" prop="">
            <el-input v-model="editForm.name" disabled></el-input>
          </el-form-item>
          <el-form-item label="" prop="">
            <el-input v-model="editForm.tel" clearable></el-input>
          </el-form-item>
          <el-form-item label="" prop="">
            <el-select v-model="editForm.roler" placeholder="">
              <el-option label="" value="0"></el-option>
              <el-option label="" value="1"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="editdialogVisible = false"> </el-button>
          <el-button type="primary" @click="editUserInfo"> </el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    // 
    var checkPhone = (rule, value, cb) => {
      // 
      // const regPhone = /^(0|86|17951)?(13[0-9]|15[0123456789]|17[678]|18[0-9]|14[57])[0-9]{8}$/
      const regPhone = /^[1][3,4,5,7,8][0-9]{9}$/;
      if (regPhone.test(value)) {
        return cb();
      }
      cb(new Error(""));
    };
    return {
      userlist: [],
      queryInfo: {
        // 
        query: "",
        // 
        pagenum: 1,
        // 
        pagesize: 7
      },
      // 
      total: 7,
      // 
      addDialogVisible: false,
      // 
      editdialogVisible: false,
      // 
      addForm: {
        userId:"",
        userName: "",
        sex: "",
        telephone: "",
        roler: ""
      },
      // 
      addFormRules: {
        userId:[
          {required:true,message:"",trigger:"blur"},
          {min:11,max:11,message:"11",trigger: "blur"}
        ],
        userName: [
          { required: true, message: "", trigger: "blur" },
          { min: 2, max: 10, message: " 2  10 ", trigger: "blur" }
        ],
        sex: [{ required: true, message: "", trigger: "blur" }],
        telephone: [
          { required: true, message: "", trigger: "blur" },
          { validator: checkPhone, trigger: "blur" }
        ],
        roler: [{ required: true, message: "", trigger: "blur" }]
      },
      // 
      editForm: {
        uname: "",
        name: "",
        tel: "",
        roler: ""
      },
      // 
      editFormRules: {
        userName: [
          { required: true, message: "", trigger: "blur" },
          { min: 2, max: 10, message: " 2  10 ", trigger: "blur" }
        ],
        telephone: [
          { required: true, message: "", trigger: "blur" },
          { validator: checkPhone, trigger: "blur" }
        ],
        roler: [{ required: true, message: "", trigger: "blur" }]
      },
      // 
      setRoleDialogVisible: false,
      // 
      userInfo: {},
      // id
      selectedRoleId:''
    };
  },
  created() {
    this.oneQuest();
  },
  methods: {
    //  table 
    formatRole(row){
      return row.roler=='0'?'':''
    },
    // 
    oneQuest() {
      var _this = this;
      this.$http
        .post("http://localhost/yhqx/search", this.queryInfo)
        .then(function(response) {
          console.log(response.data);
          if (response.data.code == 200) {
            _this.userlist = response.data.data;
            _this.total = response.data.total;
          }
        });
    },
    // pageSize
    handleSizeChange(newSize) {
      console.log(newSize);
      this.queryInfo.pagesize = newSize;
      this.oneQuest();
    },
    // 
    handleCurrentChange(newPage) {
      console.log(newPage);
      this.queryInfo.pagenum = newPage;
      this.oneQuest();
    },
    //  switch
    userStateChanged(userInfo) {
      console.log(userInfo);
      // 
    },
    // 
    search(user) {
      console.log(user);
      // 
      this.oneQuest()
    },
    // 
    addDialogClosed() {
      this.$refs.addFormRef.resetFields();
    },
    // 
    addUser() {
      var _this = this;
      this.$refs.addFormRef.validate(valid => {
        if (!valid) return;
        // 
        this.$http
        .post("http://localhost/yhqx/add", this.addForm)
        .then(function(response) {
          console.log(response.data);
          if (response.data.code == 200) {
            _this.$notify({
                title: "",
                message: "",
                type: "success"
              });
           _this.oneQuest();
           _this.addDialogVisible  = false
          }
        });

      });
    },
    // 
    showEditDialog(user) {
      this.editdialogVisible = true;
      this.editForm.uname = user.uname;
      this.editForm.name = user.name;
      this.editForm.tel = user.tel;
      this.editForm.roler = user.roler;
    },
    // 
    editDialogClosed() {
      this.$refs.editFormRef.resetFields();
    },
    // 
    editUserInfo() {
      var _this = this;
      this.$refs.editFormRef.validate(valid => {
        console.log(valid);
        if (!valid) return;
        // 
        this.$http
        .post("http://localhost/yhqx/edit", this.editForm)
        .then(function(response) {
          console.log(response.data);
          if (response.data.code == 200) {
            _this.$notify({
                title: "",
                message: "",
                type: "success"
              });
           _this.oneQuest();
           _this.editdialogVisible  = false
          }else if(response.data.code == 0){
             _this.$notify({
                title: "",
                message: "",
                type: "error"
              });
          }
        })
      });
    },
    // id
    async removeUserById(uname) {
      var _this = this;
      // 
      const cofirmResult = await this.$confirm(
        ", ?",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        }
      ).catch(err => err);
      //  confirm
      //  cancel
      // console.log(cofirmResult)
      if (cofirmResult !== "confirm") {
        return this.$message({
          showClose: true,
          message: "",
          type: "info"
        });
      }
      this.$http
        .post("http://localhost/yhqx/del", {uname})
        .then(function(response) {
          console.log(response.data);
          if (response.data.code == 200) {
            _this.$notify({
                title: "",
                message: "",
                type: "success"
              });
           _this.oneQuest();
          }else if(response.data.code == 0){
             _this.$notify({
                title: "",
                message: "",
                type: "error"
              });
          }
        })
      console.log("");
    },
    // 
    setRole(userInfo) {
      this.userInfo = userInfo;
      this.setRoleDialogVisible = true;
    },
    // 
    setRoleDialogClosed(){
      this.selectedRoleId = ''
    }
  }
};
</script>

<style lang="css" scoped>
.marginTop{
  margin-top: 15px;
}
#cz{
  display: flex;
  justify-content: space-around;
}
</style>
