<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <!--  -->
      <el-form :model="searchForm" ref="addFormRef" label-width="60px">
        <el-row>
          <el-col :span="6">
            <el-form-item label="" prop="lyNumber">
              <el-select v-model="searchForm.lyNumber" placeholder="" clearable>
                <el-option
                    v-for="item in lyNamelist"
                    :key="item.id"
                    :label="item.lynumber"
                    :value="item.lynumber"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="" prop="sushe">
              <el-input v-model="searchForm.name" placeholder="" clearable></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="3">
            <el-button type="primary" icon="el-icon-search" @click="search"></el-button>
          </el-col>
          <el-col :span="3">
            <el-button type="primary" icon="el-icon-plus" @click="addDialogVisible=true"></el-button>
          </el-col>
        </el-row>
      </el-form>
      <!--  -->
      <el-tabs type="border-card">
        <el-tab-pane label="">
          <!--  -->
          <el-table
            :data="datalist"
            stripe
            border
            maxheight="350px"
            :default-sort="{prop: 'health',prop: 'discipline',prop: 'dormitoryculture',prop: 'total', prop: 'time',order: 'descending'}"
          >
            <el-table-column label="" prop="lynumber" width="90px"></el-table-column>
            <el-table-column label="" prop="name" width="90px"></el-table-column>
            <el-table-column label="" prop="time" sortable></el-table-column>
            <el-table-column label="" prop="pgf" sortable width="110px"></el-table-column>
            <el-table-column label="" prop="fwf" sortable width="110px"></el-table-column>
            <el-table-column label="" prop="zrx" sortable></el-table-column>
            <el-table-column label="" prop="total" sortable width="110px"></el-table-column>
          </el-table>
          <!--  -->
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="queryInfo.pagenum"
            :page-sizes="[1, 2, 5, 10]"
            :page-size="queryInfo.pagesize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="total"
          ></el-pagination>
        </el-tab-pane>
        <el-tab-pane label="" name="second">
          <!--  -->
          <div id="myChart"></div>
        </el-tab-pane>
      </el-tabs>
      <!--  -->
      <el-dialog title="" :visible.sync="addDialogVisible" width="50%" @close="addDialogClosed">
        <!--  -->
        <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="80px">
          <el-form-item label="" prop="lyNumber">
            <el-select v-model="addForm.lyNumber" placeholder="" clearable>
              <el-option
                  v-for="item in lyNamelist"
                  :key="item.id"
                  :label="item.lynumber"
                  :value="item.lynumber"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="name">
            <el-input v-model="addForm.name" disabled></el-input>
          </el-form-item>
          <!--  -->
            <el-form-item label="" prop="data">
              <el-date-picker
                v-model="addForm.data"
                type="date"
                value-format="yyyy-MM-dd"
                placeholder=""
                :picker-options="pickerOptions"
              ></el-date-picker>
            </el-form-item>
          <el-form-item label="" prop="time">
              <el-time-picker v-model="addForm.time" value-format="HH:mm:ss" placeholder=""></el-time-picker>
            </el-form-item>
          <el-form-item label="" prop="pgf">
            <el-input v-model="addForm.pgf"></el-input>
          </el-form-item>
          <el-form-item label="" prop="fwf">
            <el-input v-model="addForm.fwf"></el-input>
          </el-form-item>
          <el-form-item label="" prop="zrx">
            <el-input v-model="addForm.zrx"></el-input>
          </el-form-item>
        </el-form>
        <!--  -->
        <span slot="footer" class="dialog-footer">
          <el-button @click="addDialogVisible = false"> </el-button>
          <el-button type="primary" @click="addUser"> </el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 
      lyNamelist:[],
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now();
        },
        shortcuts: [
          {
            text: "",
            onClick(picker) {
              picker.$emit("pick", new Date());
            }
          }
        ]
      },
      // 
      searchForm: {
        lyNumber: "",
        name: ""
      },
      // 
      addForm: {
        lyNumber: "",
        name:'',
        pgf:'',
        fwf:'',
        zrx:'',
        // 
        data: "",
        // 
        time: ""
      },
      // 
      addFormRules: {
        name: [
          { required: true, message: "", trigger: "blur" },
          { min: 2, max: 10, message: " 2  10 ", trigger: "blur" }
        ],
        data: [{ required: true, message: "", trigger: "blur" }],
        time: [{ required: true, message: "", trigger: "blur" }],
        fwf: [
          { required: true, message: "", trigger: "blur" }
        ],
        zrx: [
          { required: true, message: "", trigger: "blur" }
        ],
        pgf: [
          { required: true, message: "", trigger: "blur" }
        ],
        lyNumber: [
          {
            required: true,
            message: ",*",
            trigger: "blur"
          }
        ]
      },
      // 
      datalist: [],
      queryInfo: {
        // 
        pagenum: 1,
        // 
        pagesize: 5
      },
      // 
      sushe: [],
      // 
      health: [],
      // 
      discipline: [],
      // 
      culture: [],
      // 
      total: 0,
      // 
      activeName: "second",
      // 
      addDialogVisible: false,
    };
  },
  created() {
    this.oneQuest();
    var _this=this;
    this.$http
          .post("http://localhost/buildings/lyname")
          .then(function(response) {
            console.log(response.data);
            if (response.data.code == 200) {
              _this.lyNamelist = response.data.data;
            }
          });
  },
  methods: {
    // 
    oneQuest() {
      var _this = this;
      this.$http
        .post("http://localhost/lgpf/search", {
          lynumber: _this.searchForm.lyNumber,
          name: _this.searchForm.name,
          pagenum: _this.queryInfo.pagenum,
          pagesize: _this.queryInfo.pagesize
        })
        .then(function(response) {
          console.log(response.data);
          if (response.data.code == 200) {
            _this.datalist = response.data.data;
            _this.total = response.data.total;
            _this.sushe = response.data.lgname;
            _this.health = response.data.pgf;
            _this.discipline = response.data.fwf;
            _this.culture = response.data.zrx;
            //  echarts 
            _this.drawColumn();
          }
        });
    },
    // 
    search(){
      this.oneQuest();
    },
    // pageSize
    handleSizeChange(newSize) {
      console.log(newSize);
      this.queryInfo.pagesize = newSize;
      this.oneQuest();
    },
    // 
    handleCurrentChange(newPage) {
      console.log(newPage);
      this.queryInfo.pagenum = newPage;
      this.oneQuest();
    },
    // 
    addDialogClosed() {
      // this.$refs.addFormRef.resetFields();
    },
    //
    addUser() {
      var _this = this;
      this.$refs.addFormRef.validate(valid => {
        console.log(valid);
        if (!valid) return;
        // 
        // this.studentlist.unshift(this.addForm);
        this.$http
          .post("http://localhost/lgpf/add", this.addForm)
          .then(function(response) {
            console.log(response.data);
            if (response.data.code == 200) {
              _this.oneQuest();
              _this.$message({
                showClose: true,
                message: "",
                type: "success"
              });
            }else{
              _this.$message({
                showClose: true,
                message: "",
                type: "error"
              });
            }
          });
        this.addDialogVisible = false;
      });
    },

    // echarts
    drawColumn() {
      let myChart = this.$echarts.init(document.getElementById("myChart"));
      var option = {
        title: {
          text: "",
          left: "center",
          top: "auto"
        },
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "cross",
            crossStyle: {
              color: "#999"
            }
          }
        },
        toolbox: {
          feature: {
            dataView: { show: true, readOnly: false },
            magicType: { show: true, type: ["line", "bar"] },
            restore: { show: true },
            saveAsImage: { show: true }
          }
        },
        legend: {
          data: ["", "", ""],
          top: "30px"
        },
        xAxis: [
          {
            type: "category",
            name: "",
            data: this.sushe,
            axisPointer: {
              type: "shadow"
            }
          }
        ],
        yAxis: [
          {
            type: "value",
            name: "",
            min: 0,
            max: 10,
            interval: 2,
            axisLabel: {
              formatter: "{value} "
            }
          },
          {
            type: "value",
            name: "",
            min: 0,
            max: 10,
            interval: 2,
            axisLabel: {
              formatter: "{value} "
            }
          },
          {
            type: "value",
            name: "",
            min: 0,
            max: 10,
            position: "right",
            offset: 50,
            axisLine: {
              lineStyle: {
                color: "red"
              }
            },
            axisLabel: {
              formatter: "{value} "
            }
          }
        ],
        series: [
          {
            name: "",
            type: "bar",
            data: this.health
          },
          {
            name: "",
            type: "bar",
            data: this.discipline
          },
          {
            name: "",
            type: "line",
            yAxisIndex: 1,
            data: this.culture
          }
        ]
      };
      myChart.setOption(option);
    }
  },
  watch: {
    'addForm.lyNumber': function(newVal) {
      this.addForm.name="";
      //  HTTP  addForm.lyNumber 
      this.$http
          .post("http://localhost/lgpf/getSuguan", { lynumber: newVal })
          .then(response => {
            console.log(response.data);
            this.addForm.name = response.data.data;
          })
          .catch(error => {
            // 
            console.error(':', error);
          });
    }
  },

};
</script>

<style lang="css" scoped>
.el-button {
  margin-left: 20px;
}
#myChart {
  width: 950px;
  height: 450px;
}
</style>
