<script>
export default {
  data() {
    // 
    var checkPhone = (rule, value, cb) => {
      // 
      // const regPhone = /^(0|86|17951)?(13[0-9]|15[0123456789]|17[678]|18[0-9]|14[57])[0-9]{8}$/
      const regPhone = /^[1][3,4,5,7,8][0-9]{9}$/;
      if (regPhone.test(value)) {
        return cb();
      }
      cb(new Error(""));
    };
    return {
      // 
      xyList:[],
      // 
      lyNamelist:[],
      // 
      studentlist: [],
      // 
      studentNumber: "",
      // 
      studentName: "",
      // 
      susheNumber: "",
      // 
      xueyuan: "",
      // 
      lyNumber: "",
      // 
      className: "",
      queryInfo: {
        // 
        query: "",
        // 
        pagenum: 1,
        // 
        pagesize: 5
      },
      // 
      total: 0,
      // 
      addDialogVisible: false,
      // 
      addForm: {
        pass: "123456",
        state: 1,
        stuNumber: "",
        stuName: "",
        sex: "",
        tel: "",
        sushe: "",
        xueyuan: "",
        banji: "",
        lyNumber: "",
        fdy: "",
        fdytel: "",
        family: "",
        familytel: ""
      },
      // 
      addFormRules: {
        stuName: [
          { required: true, message: "", trigger: "blur" },
          { min: 2, max: 10, message: " 2  10 ", trigger: "blur" }
        ],
        sex: [{ required: true, message: "", trigger: "blur" }],
        tel: [
          { required: true, message: "", trigger: "blur" },
          { validator: checkPhone, trigger: "blur" }
        ],
        stuNumber: [
          { required: true, message: "", trigger: "blur" },
          { min: 11, max: 11, message: "11", trigger: "blur" }
        ],
        xueyuan: [
          { required: true, message: "", trigger: "blur" }
        ],
        banji: [{ required: true, message: "", trigger: "blur" }],
        fdy: [{ required: true, message: "", trigger: "blur" }],
        fdytel: [
          { required: true, message: "", trigger: "blur" }
        ],
        family: [
          { required: true, message: "", trigger: "blur" }
        ],
        familytel: [
          { required: true, message: "", trigger: "blur" }
        ],
        lyNumber: [
          {
            required: true,
            message: ",*",
            trigger: "blur"
          }
        ],
        sushe: [
          { required: true, message: "101", trigger: "blur" }
        ]
      },
      // /
      viewdialogVisible: false,
      // 
      viewForm: {
        id: "",
        uname: "",
        name: "",
        sex: "",
        tel: "",
        sushe: "",
        xueyuan: "",
        banji: "",
        lynumber: "",
        fdy: "",
        fdytel: "",
        family: "",
        familytel: ""
      }
    };
  },
  created() {
    this.oneQuest();
    let _this=this;
    this.$http
        .post("http://localhost/studentsList/xyname")
        .then(function(response) {
          if (response.data.code == 200) {
            _this.xyList = response.data.data;
          }
        });
    this.$http
        .post("http://localhost/buildings/lyname")
        .then(function(response) {
          console.log(response.data);
          if (response.data.code == 200) {
            _this.lyNamelist = response.data.data;
          }
        });
  },
  methods: {
    // 
    oneQuest() {
      var _this = this;
      this.$http
        .post("http://localhost/studentsList/search", {
          studentNumber: this.studentNumber,
          studentName: this.studentName,
          susheNumber: this.susheNumber,
          xueyuan: this.xueyuan,
          lyNumber: this.lyNumber,
          className: this.className,
          pagenum: this.queryInfo.pagenum,
          pagesize: this.queryInfo.pagesize
        })
        .then(function(response) {
          console.log(response.data);
          if (response.data.code == 200) {
            _this.studentlist = response.data.data;
            _this.total = response.data.total;
          }
        });
    },
    search() {
      var _this = this;
      this.$http
        .post("http://localhost/studentsList/search", {
          studentNumber: this.studentNumber,
          studentName: this.studentName,
          susheNumber: this.susheNumber,
          xueyuan: this.xueyuan,
          lyNumber: this.lyNumber,
          className: this.className,
          pagenum: 1,
          pagesize: this.queryInfo.pagesize
        })
        .then(function(response) {
          console.log(response.data);
          if (response.data.code == 200) {
            _this.studentlist = response.data.data;
            _this.total = response.data.total;
            _this.queryInfo.pagenum=1;
          }
        });
    },
    // pageSize
    handleSizeChange(newSize) {
      console.log(newSize);
      this.queryInfo.pagesize = newSize;
      this.oneQuest();
    },
    // 
    handleCurrentChange(newPage) {
      console.log(newPage);
      this.queryInfo.pagenum = newPage;
      this.oneQuest();
    },
    // ----------Excel--------------
    // 
    onChange(file, fileList) {
      console.log(fileList);
      this.fileData = file; // 
      this.readExcel(); // 
    },
    // 
    readExcel(e) {
      console.log(e);
      let that = this;
      const files = that.fileData;
      console.log(files);
      if (!files) {
        //
        return false;
      } else if (!/\.(xls|xlsx)$/.test(files.name.toLowerCase())) {
        this.$message.error("xlsxlsx");
        return false;
      }
      const fileReader = new FileReader();
      fileReader.onload = ev => {
        try {
          const data = ev.target.result;
          const workbook = this.XLSX.read(data, {
            type: "binary"
          });
          console.log(workbook.SheetNames);
          const wsname = workbook.SheetNames[0]; //
          const ws = this.XLSX.utils.sheet_to_json(workbook.Sheets[wsname]); //json
          console.log(ws);
          that.outputs = []; //
          for (var i = 0; i < ws.length; i++) {
            var sheetData = {
              //  el  ws[i][]
              uname: ws[i][""],
              name: ws[i][""],
              sex: ws[i][""],
              xueyuan: ws[i][""],
              banji: ws[i][""],
              tel: ws[i][""],
              lynumber: ws[i][""],
              fdy:ws[i][''],
              fdytel:ws[i][''],
              family:ws[i][''],
              familytel:ws[i][''],
            };
            that.outputs.push(sheetData);
          }
          console.log(that.outputs);
          this.$http
              .post("http://localhost/studentsList/batchAdd", that.outputs)
              .then(function(response) {
                console.log(response.data);
                if (response.data.code == 200) {
                  console.log("")
                  that.$message({
                    message: response.data.msg,
                    showClose: true,
                    type: "success"
                  });
                  this.oneQuest();
                }else{
                  that.$message({
                    message: response.data.msg,
                    showClose: true,
                    type: "error"
                  });
                }
              });
          this.$refs.upload.value = "";
        } catch (e) {
          return false;
        }
      };
      //  input  files[0]
      fileReader.readAsBinaryString(files.raw);
    },
    exportTemplate(){
        require.ensure([], () => {
          const { export_json_to_excel } = require("../../excel/Export2Excel");
          const tHeader = [
            "",
            "",
            "",
            "",
            "",
            "",
            "",
            "",
            "",
            "",
            ""
          ];
          export_json_to_excel(tHeader, [], "");
        });
    },
    // 
    addDialogClosed() {
      // this.$refs.addFormRef.resetFields();
    },
    //
    addUser() {
      var _this = this;
      this.$refs.addFormRef.validate(valid => {
        console.log(valid);
        if (!valid) return;
        // 
        // this.studentlist.unshift(this.addForm);
        this.$http
          .post("http://localhost/studentsList/add", this.addForm)
          .then(function(response) {
            console.log(response.data);
            if (response.data.code == 200) {
              _this.oneQuest();
              _this.$message({
                showClose: true,
                message: "",
                type: "success"
              });
            }else{
              _this.$message({
                showClose: true,
                message: "",
                type: "error"
              });
            }
          });
        this.addDialogVisible = false;
      });
    },
    // /
    showEditDialog(user) {
      this.viewdialogVisible = true;
      console.log(user);
      // id
      this.viewForm.name = user.name;
      this.viewForm.uname = user.uname;
      this.viewForm.sex = user.sex;
      this.viewForm.tel = user.tel;
      this.viewForm.sushe = user.sushe;
      this.viewForm.xueyuan = user.xueyuan;
      this.viewForm.banji = user.banji;
      this.viewForm.lynumber = user.lynumber;
      this.viewForm.fdy = user.fdy;
      this.viewForm.fdytel = user.fdytel;
      this.viewForm.family = user.family;
      this.viewForm.familytel = user.familytel;
      this.viewForm.id = user.id;
      // id
    },
    // /
    viewDialogClosed() {
      // this.$refs.viewFormRef.resetFields();
    },
    // /
    editUserInfo() {
      var _this = this;
      this.$refs.viewFormRef.validate(valid => {
        console.log(valid);
        if (!valid) return;
        // 
        this.$http
          .post("http://localhost/studentsList/edit", this.viewForm)
          .then(function(response) {
            console.log(response.data);
            if (response.data.code == 200) {
              _this.oneQuest();
              _this.$message({
                showClose: true,
                message: "",
                type: "success"
              });
            }
            if (response.data.code == 100) {
              _this.oneQuest();
              _this.$message({
                showClose: true,
                message: "",
                type: "info"
              });
            }
            if (response.data.code == 0) {
              _this.$message({
                showClose: true,
                message: "",
                type: "error"
              });
            }
          });

        this.viewdialogVisible = false;
      });
    },
    // id
    async removeStudentById(id) {
      var _this = this;
      // 
      const cofirmResult = await this.$confirm(
        ", ?",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        }
      ).catch(err => err);
      //  confirm
      //  cancel
      // console.log(cofirmResult)
      if (cofirmResult !== "confirm") {
        return this.$message({
          showClose: true,
          message: "",
          type: "info"
        });
      }
      // 
      this.$http
        .post("http://localhost/studentsList/del", {
          id: id
        })
        .then(function(response) {
          console.log(response.data);
          if (response.data.code == 200) {
            _this.$http
              .post("http://localhost/studentsList/search", {
                studentNumber: _this.studentNumber,
                studentName: _this.studentName,
                susheNumber: _this.susheNumber,
                xueyuan: _this.xueyuan,
                lyNumber: _this.lyNumber,
                className: _this.className,
                pagenum: _this.queryInfo.pagenum,
                pagesize: _this.queryInfo.pagesize
              })
              .then(function(response) {
                console.log(response.data);
                if (response.data.code == 200) {
                  _this.studentlist = response.data.data;
                  _this.total = response.data.total;
                }
              });
            return _this.$message({
              showClose: true,
              message: "",
              type: "success"
            });
          }
        });
      console.log("");
    }
  }
};
