<script>
export default {
  data() {
    // 
    var checkPhone = (rule, value, cb) => {
      // 
      // const regPhone = /^(0|86|17951)?(13[0-9]|15[0123456789]|17[678]|18[0-9]|14[57])[0-9]{8}$/
      const regPhone = /^[1][3,4,5,7,8][0-9]{9}$/;
      if (regPhone.test(value)) {
        return cb();
      }
      cb(new Error(""));
    };
    return {
      // 
      xyList:[],
      // select 
      lynumber: "1",
      // 
      sushe: "",
      // 
      lyNamelist: [],
      // 
      lylist: [],
      // 
      sslist: [],
      // 
      expandlist: [],
      queryInfo: {
        // 
        pagenum: 1,
        // 
        pagesize: 8
      },
      // 
      total: 7,
      // rowkey
      expands: [],
      // 
      pfDialogVisible:false,
      // 
      dfDialogVisible:false,
      pfForm:{
        lynumber:'',
        sushe:'',
        date:'',
        beizi:'',
        zhuomian:'',
        xiezi:'',
        dimian:'',
        yangtai:'',
        taipen:'',
        linyufang:'',
        weishengjian:'',
      },
      dfForm:{
        lynumber:"",
        sushe:"",
        date:"",
        dianfei:""
      },
      //
      pfRules: {
        beizi: [{ required: true, message: '', trigger: 'blur' }, { validator: this.validateScore(3), trigger: 'blur' }],
        zhuomian: [{ required: true, message: '', trigger: 'blur' }, { validator: this.validateScore(3), trigger: 'blur' }],
        xiezi: [{ required: true, message: '', trigger: 'blur' }, { validator: this.validateScore(3), trigger: 'blur' }],
        dimian: [{ required: true, message: '', trigger: 'blur' }, { validator: this.validateScore(3), trigger: 'blur' }],
        yangtai: [{ required: true, message: '', trigger: 'blur' }, { validator: this.validateScore(2), trigger: 'blur' }],
        taipen: [{ required: true, message: '', trigger: 'blur' }, { validator: this.validateScore(2), trigger: 'blur' }],
        linyufang: [{ required: true, message: '', trigger: 'blur' }, { validator: this.validateScore(2), trigger: 'blur' }],
        weishengjian: [{ required: true, message: '', trigger: 'blur' }, { validator: this.validateScore(2), trigger: 'blur' }]
      },
      // 
      addDialogVisible: false,
      // 
      addForm: {
        uname: "",
        lynumber: "",
        sushe: ""
      },
      // 
      addFormRules: {
        uname: [
          { required: true, message: "", trigger: "blur" },
          { min: 11, max: 11, message: " 11 ", trigger: "blur" }
        ],
      },
      // 
      editdialogVisible: false,
      // 
      editForm: {
        name: "",
        uname: "",
        tel: "",
        fdy: "",
        fdytel: "",
        xueyuan: "",
        banji: ""
      },
      // 
      editFormRules: {
        leader: [
          { required: true, message: "", trigger: "blur" }
        ],
        tel: [
          { required: true, message: "", trigger: "blur" },
          { validator: checkPhone, trigger: "blur" }
        ]
      }
    };
  },
  // 
  created() {
    var _this = this;
    // 
    this.$http
        .post("http://localhost/studentsList/xyname")
        .then(function(response) {
          if (response.data.code == 200) {
            _this.xyList = response.data.data;
          }
        });
    // 
    this.$http
      .post("http://localhost/buildings/lyname")
      .then(function(response) {
        console.log(response.data);
        if (response.data.code == 200) {
          _this.lyNamelist = response.data.data;
        }
      });
    // 1
    // this.$http
    //   .post("http://localhost/dormList/search", {
    //     lynumber: this.lynumber,
    //     sushe: this.sushe,
    //     pagenum: this.queryInfo.pagenum,
    //     pagesize: this.queryInfo.pagesize
    //   })
    //   .then(function(response) {
    //     console.log(response.data);
    //     if (response.data.code == 200) {
    //       _this.sslist = response.data.data;
    //       _this.total = response.data.pagetotal;
    //     }
    //   });
    this.search();
  },
  methods: {
    // 
    getTime(){
      const now = new Date();
      const year = now.getFullYear();
      const month = (now.getMonth() + 1).toString().padStart(2, '0');
      const day = now.getDate().toString().padStart(2, '0');
      return year+"-"+month+"-"+day;
    },
    // 
    search() {
      var _this = this;
      // 
      this.$http
        .post("http://localhost/dormList/search", {
          lynumber: this.lynumber,
          sushe: this.sushe,
          pagenum: this.queryInfo.pagenum,
          pagesize: this.queryInfo.pagesize
        })
        .then(function(response) {
          console.log(response.data);
          if (response.data.code == 200) {
            _this.sslist = response.data.data;
            _this.total = response.data.pagetotal;
          } else {
            _this.$notify({
              title: "",
              message: "",
              type: "warning"
            });
          }
        });
    },
    // pageSize
    handleSizeChange(newSize) {
      console.log(newSize);
      this.queryInfo.pagesize = newSize;
      var _this = this;
      this.$http
        .post("http://localhost/dormList/search", {
          lynumber: this.lynumber,
          sushe: this.sushe,
          pagenum: this.queryInfo.pagenum,
          pagesize: newSize
        })
        .then(function(response) {
          console.log(response.data);
          if (response.data.code == 200) {
            _this.sslist = response.data.data;
            _this.total = response.data.pagetotal;
          }
        });
    },
    // 
    handleCurrentChange(newPage) {
      console.log(newPage);
      this.queryInfo.pagenum = newPage;
      var _this = this;
      this.$http
        .post("http://localhost/dormList/search", {
          lynumber: this.lynumber,
          sushe: this.sushe,
          pagenum: newPage,
          pagesize: this.queryInfo.pagesize
        })
        .then(function(response) {
          console.log(response.data);
          if (response.data.code == 200) {
            _this.sslist = response.data.data;
            _this.total = response.data.pagetotal;
          }
        });
    },
    //rowkey
    getRowKeys(row) {
      return row.id;
    },
    // 
    rowClik(row) {
      console.log(row.id);
      if (row.id === this.expands[0]) {
        //expands
        this.expands = [];
      } else {
        this.expands = [];
        this.expands.push(row.id);
      }
    },
    pf(sushe){
      this.pfForm.lynumber=this.lynumber;
      this.pfForm.sushe=sushe;
      // 
      this.pfForm.date=this.getTime();
      this.pfDialogVisible = true;
    },
    df(sushe){
      this.dfForm.lynumber=this.lynumber;
      this.dfForm.sushe=sushe;
      this.dfForm.date=this.getTime();
      this.dfDialogVisible = true;
    },
    pfDialogClosed(){
      this.$refs.pfFormRef.resetFields();
    },
    dfDialogClosed(){
      this.$refs.dfFormRef.resetFields();
    },
    validateScore(max) {
      return (rule, value, callback) => {
        if (value > max) {
          callback(new Error('' + max));
        } else {
          callback();
        }
      };
    },
    pfAdd() {
      // 
      this.$refs.pfFormRef.validate(valid => {
        if (valid) {
          //  POST 
          this.$http
              .post("http://localhost/dormList/pfAdd", this.pfForm)
              .then(response => {
                if (response.data.code == 200) {
                  this.$notify({
                    title: "",
                    message: "",
                    type: "success"
                  });
                  this.pfDialogVisible = false;
                } else {
                  this.$notify({
                    title: "",
                    message: "",
                    type: "error"
                  });
                  this.pfDialogVisible = false;
                }
              })
              .catch(error => {
                console.error("", error);
                this.$notify({
                  title: "",
                  message: "",
                  type: "error"
                });
              });
        } else {
          // 
          this.$notify({
            title: "",
            message: "",
            type: "error"
          });
        }
      });
    },
    dfAdd() {
      this.$http
          .post("http://localhost/dormList/dfAdd", this.dfForm)
          .then(response => {
            if (response.data.code == 200) {
              this.$notify({
                title: "",
                message: "",
                type: "success"
              });
              this.dfDialogVisible = false;
            } else {
              this.$notify({
                title: "",
                message: "",
                type: "error"
              });
              this.dfDialogVisible = false;
            }
          })
          .catch(error => {
            console.error("", error);
            this.$notify({
              title: "",
              message: "",
              type: "error"
            });
          });
    },
    // 
    addStudent(sushe, sex, lynumber) {
      this.addForm.sex = sex;
      this.addForm.sushe = sushe;
      this.addForm.lynumber = lynumber;
      this.addDialogVisible = true;
    },
    // 
    addDialogClosed() {
      this.$refs.addFormRef.resetFields();
    },
    // 
    addUser() {
      this.$refs.addFormRef.validate(valid => {
        if (!valid) return;
        // 
        // this.studentlist.unshift(this.addForm);
        var _this = this;
        this.$http
          .post("http://localhost/dormList/adduser", this.addForm)
          .then(function(response) {
            console.log(response.data);
            if (response.data.code == 200) {
              _this.$notify({
                title: "",
                message: "",
                type: "success"
              });
              // 
              // 1
              _this.$http
                .post("http://localhost/dormList/search", {
                  lynumber: _this.lynumber,
                  sushe: _this.sushe,
                  pagenum: _this.queryInfo.pagenum,
                  pagesize: _this.queryInfo.pagesize
                })
                .then(function(response) {
                  console.log(response.data);
                  if (response.data.code == 200) {
                    _this.sslist = response.data.data;
                    _this.total = response.data.pagetotal;
                  }
                });
              _this.addDialogVisible = false;
            } else {
              _this.$notify({
                title: "",
                message: response.data.msg,
                type: "error"
              });
              _this.addDialogVisible = false;
            }
          });
      });
    },
    // id
    async removelyInfoById(data) {
      console.log(data)
      var _this = this;
      // 
      const cofirmResult = await this.$confirm(
        "?",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        }
      ).catch(err => err);
      //  confirm
      //  cancel
      // console.log(cofirmResult)
      if (cofirmResult !== "confirm") {
        return this.$message({
          showClose: true,
          message: "",
          type: "info"
        });
      }
      // 
      this.$http
        .post("http://localhost/dormList/del", {
          id: data.uname,
          lynumber:data.lynumber,
          sushe:data.sushe
        })
        .then(function(response) {
          console.log(response.data);
          if (response.data.code == 200) {
            _this.$http
              .post("http://localhost/dormList/search", {
                lynumber: _this.lynumber,
                sushe: _this.sushe,
                pagenum: _this.queryInfo.pagenum,
                pagesize: _this.queryInfo.pagesize
              })
              .then(function(response) {
                console.log(response.data);
                if (response.data.code == 200) {
                  _this.sslist = response.data.data;
                  _this.total = response.data.pagetotal;
                }
              });
            return _this.$message({
              showClose: true,
              message: "",
              type: "success"
            });
          }
        });
      console.log("");
    },
    // 
    editDialogClosed() {
      this.$refs.editFormRef.resetFields();
    },
    // 
    editUserInfo() {
      this.$refs.editFormRef.validate(valid => {
        console.log(valid);
        if (!valid) return;
        // 
        this.editdialogVisible = false;
      });
    },
    // 
    showEditDialog(user) {
      this.editdialogVisible = true;
      console.log(user);
      // id
      this.editForm.name = user.name;
      this.editForm.uname = user.uname;
      this.editForm.tel = user.tel;
      this.editForm.fdy = user.fdy;
      this.editForm.fdytel = user.fdytel;
      this.editForm.xueyuan = user.xueyuan;
      this.editForm.banji = user.banji;
      // id
    },
    dianfeiConfirm(id){
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        console.log(id);
        this.$http
            .post("http://localhost/dormList/dfConfirm",{id})
            .then((response)=> {
              if (response.data.code == 200) {
                this.$message({
                  type: 'success',
                  message: '!'
                });
                this.search();
              }
            });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    }
  },
};
</script>

<style lang="css" scoped>
.el-button {
  margin-left: 30px;
}
</style>
