<template>
  <div class="bg">
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="80px">
        <el-row>
          <el-col :span="10">
            <!--  -->
            <el-form-item label="" prop="data">
              <el-input v-model="addForm.date"  disabled></el-input>
            </el-form-item>

          </el-col>
          <el-col :span="12">
            <!--  -->
            <el-form-item label="" prop="time">
              <el-input v-model="addForm.time"  disabled></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <!--  -->
        <el-row>
          <el-col :span="12">
            <el-form-item label="" prop="name">
              <el-input v-model="addForm.name" placeholder="" clearable></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="tel">
              <el-input v-model="addForm.tel" placeholder="" clearable></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="" prop="lyNumber">
<!--              <el-input v-model="addForm.lyNumber" placeholder="" clearable></el-input>-->
              <el-select v-model="addForm.lyNumber" placeholder="" clearable>
                <el-option
                    v-for="item in lyNamelist"
                    :key="item.id"
                    :label="item.lynumber"
                    :value="item.lynumber"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="sushe">
              <el-input v-model="addForm.sushe" placeholder="" clearable></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-form-item label="" prop="detail">
          <el-input
            v-model="addForm.detail"
            type="textarea"
            :autosize="{ minRows: 2, maxRows: 8}"
            placeholder=""
            clearable
          ></el-input>
        </el-form-item>
        <!-- / -->
        <el-row type="flex" justify="end">
          <el-button @click="cancel"></el-button>
          <el-button type="primary" @click="addRepairs"></el-button>
        </el-row>
      </el-form>
      <p class="tishi" style="margin-top:60px">123456</p>
      <p class="tishi"></p>
    </el-card>
  </div>
</template>

<script>
import moment from 'moment';
export default {
  data() {
    // 
    var checkPhone = (rule, value, cb) => {
      // 
      // const regPhone = /^(0|86|17951)?(13[0-9]|15[0123456789]|17[678]|18[0-9]|14[57])[0-9]{8}$/
      const regPhone = /^[1][3,4,5,7,8][0-9]{9}$/;
      if (regPhone.test(value)) {
        return cb();
      }
      cb(new Error(""));
    };
    return {
      lyNamelist:[],
      // 
      addForm: {
        name: "",
        tel: "",
        lyNumber: "",
        sushe: "",
        // 
        date: moment().format('YYYY-MM-DD'),
        // 
        time: moment().format('HH:mm:ss'),
        detail: ""
      },
      // 
      addFormRules: {
        name: [
          { required: true, message: "", trigger: "blur" },
          {message: " 3  10 ", trigger: "blur" }
        ],
        date: [{ required: true, message: "", trigger: "blur" }],
        time: [{ required: true, message: "", trigger: "blur" }],
        tel: [
          { required: true, message: "", trigger: "blur" },
          { validator: checkPhone, trigger: "blur" }
        ],
        lyNumber: [
          { required: true, message: "", trigger: "blur" }
        ],
        sushe: [
          { required: true, message: "", trigger: "blur" }
        ],
        detail: [{ required: true, message: "", trigger: "blur" }]
      }
    };
  },
  methods: {
    // 
    addRepairs() {
      var _this = this;
      this.$refs.addFormRef.validate(valid => {
        console.log(valid);
        if (!valid) return;
        // 
        this.$http
          .post("http://localhost/repairReport/add", this.addForm)
          .then(function(response) {
            console.log(response.data);
            if (response.data.code == 200) {
              _this.$message({
                showClose: true,
                message: "",
                type: "success"
              });
              _this.$refs.addFormRef.resetFields();
            }else{
              _this.$message({
                showClose: true,
                message: "",
                type: "error"
              });
            }
          })
      });
    },
    // 
    cancel(){
      this.$refs.addFormRef.resetFields();
    },
  },
  created(){
    var _this=this;
    this.$http
        .post("http://localhost/buildings/lyname")
        .then(function(response) {
          console.log(response.data);
          if (response.data.code == 200) {
            _this.lyNamelist = response.data.data;
          }
        });
  }
};
</script>

