<template>
  <div>
    <el-card>
      <div class="logo">
        <img src="../assets/img/SSPUbadge.png" alt="logo" />
        <h1></h1>
      </div>
      <!--  -->
      <el-carousel height="400px" type="card" :interval="2000">
        <el-carousel-item>
          <img src="../assets/img/home.png" alt />
        </el-carousel-item>
        <el-carousel-item>
          <img src="../assets/img/home2.png" alt />
        </el-carousel-item>
        <el-carousel-item>
          <img src="../assets/img/home3.png" alt />
        </el-carousel-item>
      </el-carousel>
      <h2 class="welcome"></h2>
    </el-card>
    <el-dialog title="" :visible.sync="DialogVisible" width="50%" @close="DialogVisible=false" :show-close="false" :close-on-press-escape="false" :close-on-click-modal="false">
      <!--  -->
      <div style="font-weight: bold;font-size: 26px">
        <div><span class="keyword">{{userData.banji}}</span><span class="keyword">{{userData.name}}</span><span class="keyword">{{userData.uname}}</span></div>
        <span class="keyword">{{userData.lynumber}}</span>
      </div>
      <!--  -->
      <el-table
          :data="sslist"
          style="width: 100%"
          stripe
          border
          :row-key="getRowKeys">
<!--          :expand-row-keys="expands"-->
        <!-- @expand-change="expandchange" -->
        <!--  -->
        <el-table-column type="expand">
          <template slot-scope="scope">
            <!-- {{scope.row}} -->
            <h3 v-text="lynumber+'  '+scope.row.sushe +''" style="text-align:center"></h3>
            <el-table :data="scope.row.child" stripe border>
              <el-table-column type="index" width="30px"></el-table-column>
              <el-table-column prop="name" label="" width="60px"></el-table-column>
              <el-table-column prop="uname" label=""></el-table-column>
              <el-table-column prop="tel" label=""></el-table-column>
              <el-table-column prop="fdy" label="" width="60px"></el-table-column>
              <el-table-column prop="fdytel" label=""></el-table-column>
              <el-table-column prop="xueyuan" label=""></el-table-column>
              <el-table-column prop="banji" label=""></el-table-column>
            </el-table>
          </template>
        </el-table-column>
        <el-table-column prop="sushe" label=""></el-table-column>
        <el-table-column prop="members" label=""></el-table-column>
        <el-table-column prop="member" label="">
          <template slot-scope="scope">
            <el-tag v-if="scope.row.isFull==0"></el-tag>
            <el-tag type="danger" v-else></el-tag>
          </template>
        </el-table-column>
        <el-table-column label="" width="350px">
          <template slot-scope="scope">
            <!--  -->
            <el-tooltip effect="dark" content="" placement="top" :enterable="false" >
              <el-button
                  type="warning"
                  icon="el-icon-plus"
                  size="mini"
                  @click="allocateUser(scope.row.sushe)"
                  v-if="scope.row.isFull==0 ? true:false">
<!--                  @click="addStudent(scope.row.sushe,scope.row.sex,scope.row.lynumber)"-->
              </el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="queryInfo.pagenum"
          :page-sizes="[1, 4, 8, 16]"
          :page-size="queryInfo.pagesize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
      ></el-pagination>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      sslist:[],
      total:0,
      lynumber:'',
      queryInfo:{
        pagenum:1,
        pagesize:8,
      },
      userData:{},
      DialogVisible:false
    };
  },
  methods: {
    // 
    search() {
      var _this = this;
      // 
      this.$http
          .post("http://localhost/dormList/search", {
            lynumber: this.userData.lynumber,
            pagenum: this.queryInfo.pagenum,
            pagesize: this.queryInfo.pagesize
          })
          .then(function(response) {
            console.log(response.data);
            if (response.data.code == 200) {
              _this.sslist = response.data.data;
              _this.total = response.data.pagetotal;
            } else {
              _this.$notify({
                title: "",
                message: "",
                type: "warning"
              });
            }
          });
    },
    // pageSize
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize;
      var _this = this;
      this.$http
          .post("http://localhost/dormList/search", {
            lynumber: this.userData.lynumber,
            sushe: this.userData.sushe,
            pagenum: this.queryInfo.pagenum,
            pagesize: newSize
          })
          .then(function(response) {
            console.log(response.data);
            if (response.data.code == 200) {
              _this.sslist = response.data.data;
              _this.total = response.data.pagetotal;
            }
          });
    },
    // 
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage;
      var _this = this;
      this.$http
          .post("http://localhost/dormList/search", {
            lynumber: this.userData.lynumber,
            sushe: this.userData.sushe,
            pagenum: newPage,
            pagesize: this.queryInfo.pagesize
          })
          .then(function(response) {
            console.log(response.data);
            if (response.data.code == 200) {
              _this.sslist = response.data.data;
              _this.total = response.data.pagetotal;
            }
          });
    },
    //rowkey
    getRowKeys(row) {
      return row.id;
    },
    // 
    async allocateUser(sushe){
      var _this = this;
      // 
      const cofirmResult = await this.$confirm(
          "?",
          "",
          {
            confirmButtonText: "",
            cancelButtonText: "",
            type: "warning"
          }
      ).catch(err => err);
      //  confirm
      //  cancel
      // console.log(cofirmResult)
      if (cofirmResult !== "confirm") {
        return this.$message({
          showClose: true,
          message: "",
          type: "info"
        });
      }
      this.$http
          .post("http://localhost/dormList/addUser", {
            uname: this.userData.uname,
            lynumber:this.userData.lynumber,
            sushe
          })
          .then(function(response) {
            console.log(response.data);
            if (response.data.code == 200) {
              _this.$http
                  .post("http://localhost/login", {
                    name: _this.userData.uname,
                    password: _this.userData.pass,
                  })
                  .then(function(response) {
                    // 2000
                    if (response.data.code == "200") {
                      _this.DialogVisible=false;
                      // ,
                      _this.$message({
                        showClose: true,
                        message: "",
                        type: "success"
                      });
                      var userData = JSON.stringify(response.data.data[0]);
                      window.sessionStorage.setItem('userData',userData);
                      var a1 = response.data.route;
                      //    vuex 
                      _this.$store.commit('rolerRouter',a1)
                      // 
                      _this.$router.push('/home');
                    }
                  })
            }
          });
      console.log("");
    }
  },
  created() {
    const value = window.sessionStorage.getItem('userData');
    this.$data.userData = JSON.parse(value); //  JSON 
    if(this.$data.userData.roler==2&&!this.$data.userData.sushe){
      this.$data.DialogVisible=true;
    }
    var _this=this;
    // 1
    this.$http
        .post("http://localhost/dormList/search", {
          lynumber: this.userData.lynumber,
          sushe:'',
          pagenum: this.queryInfo.pagenum,
          pagesize: this.queryInfo.pagesize
        })
        .then(function(response) {
          console.log(response.data);
          if (response.data.code == 200) {
            _this.sslist = response.data.data;
            _this.total = response.data.pagetotal;
          }
        });
  }
};
</script>

