<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right" style="line-height: 40px">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-button type="primary" round @click="addDialogVisible=true"></el-button>
    </el-breadcrumb>

    <div class="forum-list">
      <forumComp
          v-for="topic in forumTopics"
          :key="topic.topicId"
          :topic="topic"
          @reload-data="oneQuest"
      />
    </div>
    <!--  -->
    <el-pagination
        @current-change="handleCurrentChange"
        :current-page="pagenum"
        layout="total, prev, pager, next"
        :total="total"
    ></el-pagination>
    <!--  -->
    <el-dialog title="" :visible.sync="addDialogVisible" width="50%" @close="addDialogClosed">
      <!--  -->
      <el-form :model="addForm" ref="addFormRef" label-width="80px">
        <el-form-item label="" prop="hostId">
          <el-input v-model="addForm.hostId" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="hostName">
          <el-input v-model="addForm.hostName" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="topic">
          <el-input v-model="addForm.topic"></el-input>
        </el-form-item>
        <el-form-item label="" prop="type">
        <el-select v-model="addForm.type" placeholder="">
          <el-option label="" value="0"></el-option>
          <el-option label="" value="1"></el-option>
        </el-select>
        </el-form-item>
        <el-form-item label="" prop="content">
        <el-input
            v-model="addForm.content"
            style="width: 80%"
            :autosize="{ minRows: 5, maxRows: 10 }"
            type="textarea"
            placeholder=""
        />
        </el-form-item>
      </el-form>
      <!--  -->
      <span slot="footer" class="dialog-footer">
          <el-button @click="addDialogVisible = false"> </el-button>
          <el-button type="primary" @click="addTopic"> </el-button>
        </span>
    </el-dialog>
  </div>
</template>

<script>
import forumComp from './forumComp';
import { nanoid } from 'nanoid';
export default {
  name: 'myForum',
  components: {
    forumComp,
  },
  data() {
    return {
      forumTopics: [
      ],
      pagenum:1,
      total:0,
      addDialogVisible:false,
      addForm:{
        hostId:'',
        hostName:'',
        topicId:'',
        topic:'',
        type:'',
        content:''
      }
    };
  },
  created() {
    this.oneQuest();
    const value = window.sessionStorage.getItem('userData');
    const userData = JSON.parse(value); //  JSON 
    this.$data.addForm.hostId = userData.uname;
    this.$data.addForm.hostName = userData.name;
  },
  methods:{
    oneQuest() {
      var _this = this;
      this.$http
          .post("http://localhost/myForum/search", {pagenum:this.pagenum})
          .then(function(response) {
            console.log(response.data);
            if (response.data.code == 200) {
              _this.forumTopics = response.data.data;
              _this.total = response.data.total;
            }
          });
    },
    handleCurrentChange(newPage) {
      console.log(newPage);
      this.pagenum = newPage;
      this.oneQuest();
    },
    addDialogClosed() {
      this.$refs.addFormRef.resetFields();
    },
    addTopic(){
      this.addForm.topicId=nanoid(12);
      var _this = this;
      this.$http
          .post("http://localhost/myForum/addTopic", {addForm:this.addForm})
          .then(function(response) {
            if (response.data.code == 200) {
              _this.$message({
                showClose: true,
                message: "",
                type: "success"
              });
            }else{
              _this.$message({
                showClose: true,
                message: "",
                type: "error"
              });
            }
          });
      this.addDialogVisible=false;
      this.oneQuest();
    }
  }
};
</script>

<style scoped>
.forum-list {
  margin-top: 10px;
  /*width: 70%;*/
  height: 80vh; /*  80% */
  overflow-y: auto; /*  */
  overflow-x: hidden; /*  */
}
</style>
